<template>
 <div>
    <button type="button" v-on:click="backPage()" v-if="canBack"   class="backward">قبلی</button>
    <button type="button" v-on:click="nextPage()"   class="forward">بعدی</button>
  </div>
</template>

<script>
export default {
  name:'controllerButton',
  props:{
    canBack:{default:true}
  },
  methods: {
    backPage()
    {
      this.$emit("goBack");
    },
    nextPage()
    {
      this.$emit("goNext");
    },
  },
}
</script>