            "text":"<p class=\"ql-direction-rtl\"><strong style=\"background-color: rgb(255, 255, 255); color: black;\">Q12.a-</strong><span style=\"background-color: rgb(255, 255, 255); color: black;\"> شما قبل تر گفتید که برند اصلی شما {{value1}} </span><strong style=\"background-color: rgb(255, 255, 255); color: black;\"><em>(برند علامت خورده در Q9.b)</em></strong><span style=\"background-color: rgb(255, 255, 255); color: black;\"> است. لطفا تمام دلایل خود برای استفاده از این برند به عنوان برند اصلی خود را بگویید و تا حد ممکن در پاسخ­های خود به جزییات اشاره کنید. </span><strong style=\"background-color: rgb(255, 255, 255); color: black;\">(چند پاسخی)</strong></p><p class=\"ql-direction-rtl\"><strong style=\"background-color: rgb(255, 255, 255); color: rgb(255, 0, 0);\">پرسشگر : عینا گفته های پاسخگو را یادداشت کنید.</strong></p><p class=\"ql-direction-rtl ql-align-right\"><br></p>",
            "min":0,
