                {
                  "id":5,
                  "text":"بوی تمیزی می دهد و احساس پاکیزگی به شما می دهد"
                },
                {
                  "id":4,
                  "text":"رایحه ای ملایم دارد"
                },
                {
                  "id":10,
                  "text":"یادآور رایحه های لوکس/ برتر است"
