<template>
  <div> 
    <div  style="display: none">
        {{setState}}
    </div>     
    <table style="text-align: right;" class="col-lg-12">
      <template v-for="(answ,index1) in question.answers ">
        <tr>
          <td v-if="question.showNumber">{{index1+1}}</td>
          <td v-if="answ.url" style="width: 45px;">
            <img v-bind:src="answ.url" style="width: 40px;"></img>
          </td> 
          <td >  
            <div class="form-group radio_input">
              <label v-on:click="radioClick(answ.id)" style="width: 100%">
                <div class="row">
                  <div class="col-1 radiouncheck" v-if="!data[answ.id]"></div>
                  <div class=" col-1 radiocheck" v-if="data[answ.id]"></div>
                  <span class=" col-10"> 
                    {{answ.text}}
                  </span>
                </div>
              </label>
            </div>
          </td>
        </tr>
      </template>
      <template v-if="question.otherAnswers">
        <tr v-for="(answ,index1) in question.otherAnswers ">
          <td v-if="question.showNumber">{{question.answers.length+ index1}}</td> 
          <td >
            <div class="form-group radio_input">
              <label style="width: 100%">
                <div class="row">
                  <div class=" col-2 ">
                    <span v-if="!question.place ">{{answ.text}}</span>
                  </div>
                  <span class=" col-10">
                    <input  v-if="question.place" type="text" @keydown="keydown()" class="form-control" v-model="otherData[answ.id]" v-bind:placeholder="answ.text"/>
                    <input  v-if="!question.place" type="text" @keydown="keydown()" class="form-control" v-model="otherData[answ.id]"/>
                  </span>
                </div>
              </label>
            </div>
          </td>
        </tr>
      </template>

    </table>
  </div>
</template>


<script>
export default {
  name:'radioPage',
  data() {
    return {
      setState:0,
      data:{},
      otherData:{}
    }
  },
  methods: {
    check()
    {
      var isValid =false;
      console.log('---------',this.question.expected,this.data)
      if(this.question.expected) 
      {
        for(var a in this.data)
          isValid=true; 
        for(var a in this.otherData)
          if(this.otherData[a])
            isValid=true; 
      }
      else
        isValid=true;
      if(isValid)
      {
        return {data:this.data,otherData:this.otherData}
      }
      return isValid;
    },
    keydown()
    {
      this.data={}
      this.setState++
    },
    radioClick(answerId)
    {
      this.otherData={}
      this.data ={}
      this.data[answerId]=true
      var self=this
      setTimeout(function(){

        if(self.question.autoNext)
        {
          self.$emit('next');
        }
      self.setState++
      },300)
      this.setState++

    }
  },
  props:{
    question:{default:{}}
  }
}
</script>