<template>
  <div> 
    <input id="website" name="website" type="text" value="" />
    <div id="middle-wizard">
      <div class="submit step" style="text-align: center">
        <h4 style="text-align: justify" class="main_question">
          <strong  v-if="questionData.isViewPaging">{{pageCount+1}}/{{questionData.questions.length}}</strong>
          <div style="line-height: normal;" v-if="page.tempText"  v-html="page.tempText"></div>
          <div style="line-height: normal;" v-else  v-html="page.text"></div>
          <div  v-html="trackingCode"></div>
        </h4>
    <controllerButton  v-if="false" :canBack="pageCount>1" @goBack="goBack()" @goNext="goNext()" />


        <div  style="display: none">
            {{setState}}
        </div> 
        <div v-if="page.kind=='startPage'">
          <acceptPage  :description="page.description" :buttonTitle="page.buttonTitle" @next="begin()"/> 
        </div>
        <div v-if="page.kind=='endPage'">
          <acceptPage :disableButton="issend" :description="page.description" :buttonTitle="page.buttonTitle" @next="end()"/> 
        </div>
        <div v-if="page.kind=='decimal'">
          <decimalPage  :question="page" ref="element" :key="page.id" /> 
        </div>
        <div v-if="page.kind=='radio'">
          <radioPage  :question="page" ref="element" :key="page.id" @next="goNext()" /> 
        </div>
        <div v-if="page.kind=='check'">
          <checkPage  :question="page" ref="element" :key="page.id" /> 
        </div>
        <div v-if="page.kind=='multiLine'">
          <multiLinePage  :question="page" ref="element" :key="page.id" /> 
        </div>
        <div v-if="page.kind=='singleLine'">
          <singleLinePage  :question="page" ref="element" :key="page.id" /> 
        </div> 

      </div>
    </div>   
    
    <controllerButton  v-if="showBottom" :canBack="this.page.canBack==true" @goBack="goBack()" @goNext="goNext()" />  
  </div>
</template>

<script>
import acceptPage from './acceptPage'
import decimalPage from './decimalPage'
import controllerButton from './controllerButton'
import radioPage from './radioPage'
import checkPage from './checkPage'
import multiLinePage from './multiLinePage'
import singleLinePage from './singleLinePage'
export default {
  name:"questionHandler",
  components:{acceptPage,decimalPage,controllerButton,radioPage,checkPage,multiLinePage,singleLinePage},
  
  data() {
    return { 
      tempArray:{},
      showBottom:false,
      issend:false,
      requestData:{},
      canBack:true, 
      setState:0,
      pageCount:0,
      page:{},
      trackingCode:"",
      answers:{},
      pageStack:[],
      nameId:{},
      startDate:''
    }
  },
  props:{
    questionData:{default:{}}
  },
  methods: {
    getNameById(id){
      
            console.log('ssssssssssssssssss',id)
            console.log('ssssssssssssssssss',this.nameId[id])
      if(this.nameId[id])
      {
        return this.nameId[id]
      }
      return {}
    },
    goNextPage()
    {
      var self=this;
      window.scrollTo(0, 0);

      this.pageCount++;
      //Find Next Question
      for(var m=0;m< this.questionData.conditions.length;m++)
      {
        var a=this.questionData.conditions[m]
        //console.log('condition----------->',a.stringCondition)
        var isvalid=eval(a.stringCondition)
        if(a.target.questionId=="1035")
        {
          console.log('condition----------->',isvalid)
          console.log('condition----------->',this.answers)
          console.log('condition----------->',a.stringCondition)
        }
        //console.log('condition----------->',isvalid)
        if(isvalid)
        {
          if(a.target.type=="notShow")
          {
            var tempPage=this.questionData.questions[this.pageCount]
            if(tempPage.id==a.target.questionId)
            {
              m=0;
              this.pageCount++;
            }
          }
          if(a.target.type=="goEnd")
          {
            this.pageCount=this.questionData.questions.length-1
          }
        }
      }
      this.pageStack.push(this.pageCount);
      this.page=this.questionData.questions[this.pageCount];
          console.log('nest page ----------->',this.pageCount,this.page)

      //Set Dynamic Values
      if(this.page.dynamicValues)
      {
        var text=this.page.text;
        for(var y in this.page.dynamicValues)
        {
          
          var val=eval(this.page.dynamicValues[y])
          text=text.replace('{{'+y+'}}',val)
              console.log('--------------->',val)
        }
        this.page.tempText=text;
      }

      // Check to Show Next or Back Button
      if(this.pageCount>0 && this.pageCount<this.questionData.questions.length-1)
      {
        this.showBottom=true;
      }
      else
      {
        this.showBottom=false;
      }
      //Check Answer Condition
      if(this.page.answers)
      {//condition
        for(var a of this.page.answers)
        {
          if(a.condition)
          {
            console.log('condition -------->',a)
            a.disable=!(eval(a.condition));
            console.log('condition -------->',a)
            
          }
        }
      }
      //Create External
      if(this.page.external && this.page.external.length) 
      {
        for(var a=this.page.answers.length-1;a>=0;a--)
        {
          var q= this.page.answers[a];
          if(q.isExternal)
          {
            this.page.answers.splice(a,1);
          }
        }
        for(var a of this.page.external)
        {
          if(a.type=="tempArray")
          {
              this.page.answers = this.page.answers.concat(this.tempArray[a.name])
          }
          else
          {
          //Find Question By Id
          var external=this.questionData.questions.filter(p=>p.id==a.id);
          if(external.length)
          {
            external=external[0];
            //Get Answers Of Question
            var answer=this.answers[external.id]
            for(var b of external.answers)
            {
              var exist=answer.data[b.id];
              var isAdd=false;
              if(exist && a.type=="selected")isAdd=true;
              if(!exist && a.type=="notSelected")isAdd=true;
              if(a.type=="all")isAdd=true;
              if(isAdd)
              {
                var obj={
                  text:b.text,
                  isExternal:true,
                  ref:{question:a.id,answer:b.id}
                }
                if(b.root)
                {
                  obj.root=b.root;
                }
                else
                {
                  obj.root={question:a.id,answer:b.id};
                }
                obj.id= 'ex'+obj.root.question+'_'+obj.root.answer
                this.page.answers.push(obj);
                  this.nameId[obj.id]=obj
              }
            } 
            if(a.type=="selected" && answer.otherData)
            {
              
              for(var b in answer.otherData)
              {
                var value=answer.otherData[b]
                if(value)
                {
                  var obj={
                    text:value,
                    isExternal:true,
                    root:{question:a.id,answer:'o'+b},
                    ref:{question:a.id,answer:'o'+b},
                  }
                  obj.id= 'ex'+obj.root.question+'_'+'o'+b
                  this.page.answers.push(obj);
                  this.nameId[obj.id]=obj

                }
              }
            } 
          }

          }
        }
      }
      //Create Other Answers
      if(this.page.other)
      {
        this.page.otherAnswers=[]
        for(var a=0;a<this.page.otherNum;a++)
        {
          this.page.otherAnswers.push({
            id:a.toString(),
            text:this.page.otherText,
            isOther:true
          })
        }
      }

      if(this.page.rotation)
      {
        var newArray=[]
        var isNon=[]
        while(this.page.answers.length)
        {

          var r= Math.floor(Math.random()*this.page.answers.length)
          var selecedObject=this.page.answers.splice(r,1)[0]
          if(selecedObject.isNon)
          {
            isNon.push(selecedObject)
          }
          else
          {
            newArray.push(selecedObject)
          }
        }
        this.page.answers=newArray.concat(isNon)
      }


    },
    begin()
    { 
      this.goNextPage();
      this.startDate=new Date();
      this.questionData.startDate=new Date();
    },
    end()
    {
      var response={
        questionnaireId:this.questionData.id,
        startDate:this.startDate,
        endDate:new Date(),
        answer:[]
      }
      for(var a in this.answers)
      {//tempText
        var question=this.questionData.questions.filter(p=>p.id==a)[0]
        var tempAnswer=this.answers[a]
        var answer={
          id:a,
          text:(question.tempText)?question.tempText:question.text,
          kind:question.kind,
          answer:[

          ]
        }
        if(tempAnswer.otherData)
          for(var x in tempAnswer.otherData)
          {
            if(tempAnswer.otherData[x])
              answer.answer.push({
                id:'o'+a+'_'+x,
                text:tempAnswer.otherData[x],
                answer:tempAnswer.otherData[x]
              })

          }
        for(var x in tempAnswer.data)
        {
          var tmp =tempAnswer.data[x];
          var qa=question.answers.filter(p=>p.id==x)[0]
          var id=x
          if(id.indexOf('_')==-1)
          {
            id=a+'_'+id
          }
          if(question.kind=="radio" || question.kind=="check")
          {
            if(tmp)
              answer.answer.push({
                id:id,
                text:qa.text,
                answer:qa.text
              })
          }
          else if(question.kind=="decimal" || question.kind=="singleLine"|| question.kind=="multiLine")
          {
            if(tmp)
              answer.answer.push({
                id:id,
                text:qa.text,
                answer:tmp
              })

          }
        }
         response. answer.push(answer)
      }
      
      var strurl='http://api.tickado.ir:8880/v1/a/public'; 
      var q=httpPostData(strurl,response,{
        'x-auth-token':""
      }) 

      if(q.trackingCode)
      {
        this.page.text="اطلاعات ارسال شد" +" کد پیگیری شما  به صورت زیر می باشد"
        this.page.description=""
        this.trackingCode =q.trackingCode
        this.issend=true

      }
      else
      {
        this.page.text="کد پیگیری دریافت نشد دوباره ارسال کنید"
      }

    },
    goBack()
    {
      if(this.pageStack.length<2)
        return
      if(!this.page.canBack)  
      {
        return
      }
      var self=this;
      window.scrollTo(0, 0);

      if(this.page.tempArray)
        {
          var name=this.page.tempArray.arrayName 
          var item=this.page.tempArray.item
          var allansw=this.answers
          if(this.tempArray[name])
          {
            for(var a=0;a<this.tempArray[name].length;a++)
            {
              if(this.tempArray[name][a].id==item.id)
              {
                this.tempArray[name].splice(a,1)
                break;
              }
            } 
          }
        }
      delete this.answers[this.page.id]
      this.pageStack.splice(this.pageStack.length-1,1);
      this.pageCount=this.pageStack[this.pageStack.length-1]
      this.page=this.questionData.questions[this.pageCount]
      this.setState++ 
      setTimeout(()=>{ 
        
        this.$refs.element.data=this.answers[this.page.id].data
        if(this.answers[this.page.id].otherData)
          this.$refs.element.otherData=this.answers[this.page.id].otherData
      },300)
      //var chData = this.$refs.element.check();
    },
    goNext()
    {
      var chData = this.$refs.element.check();
      if(chData)
      {
        this.answers[this.page.id]=chData;
        if(this.page.tempArray)
        {
          var name=this.page.tempArray.arrayName
          var cond=this.page.tempArray.condition
          var item=this.page.tempArray.item
          var allansw=this.answers
          var valid=eval(cond) 
          if(valid)
          {
            if(!this.tempArray[name])this.tempArray[name]=[]
            if(!item.text)
            {
              var findAnswer=this.getNameById(item.id)
              if(findAnswer.text)
                item.text=findAnswer.text
            }
            var exist=this.tempArray[name].filter(p=>p.id==item.id)
            if(!exist.length)
              this.tempArray[name].push(item)
          }
        }
        this.goNextPage();
      }
    },
  },
  mounted() {

    console.log(JSON.stringify(this.questionData,null,4))
    for(var a of this.questionData.questions)
      if(a.answers)
        for(var b of a.answers)
          b.id=b.id.toString();

    this.page=this.questionData.questions[this.pageCount];
  },
}
</script>