<template>
  <div>
    <div  style="display: none">
        {{setState}}
    </div>     
    <table style="text-align: right;" class="col-lg-12">
      <template v-for="(answ,index1) in question.answers ">
        <tr v-if="!answ.disable" >
          <td v-if="question.showNumber">{{index1+1}}</td>
          <td v-if="answ.url" style="width: 45px;">
            <img v-bind:src="answ.url" style="width: 40px;"></img>
          </td> 
          <td  >
            <div class="form-group radio_input">
              <label v-on:click="checkClick(answ.id,answ.isNon)" style="width: 100%">
                <div class="row">
                  <div class="col-1 checkboxuncheck" v-if="!data[answ.id]"></div>
                  <div class=" col-1 checkboxcheck" v-if="data[answ.id]"></div>
                  <span class=" col-10">
                                          
                                      {{answ.text}}
                                      </span>
                </div>
              </label>
            </div>
          </td>
        </tr>
      </template>
      <template v-if="question.otherAnswers">
        <tr v-for="(answ,index1) in question.otherAnswers ">
          <td v-if="question.showNumber">{{question.answers.length+ index1}}</td> 
          <td >
            <div class="form-group radio_input">
              <label style="width: 100%">
                <div class="row">
                  <div class=" col-2 ">
                    <span v-if="!question.place ">{{answ.text}}</span>
                  </div>
                  <span class=" col-10">
                    <input  v-if="question.place" type="text" class="form-control" v-model="otherData[answ.id]" v-bind:placeholder="answ.text"/>
                    <input  v-if="!question.place" type="text" class="form-control" v-model="otherData[answ.id]"/>
                  </span>
                </div>
              </label>
            </div>
          </td>
        </tr>
      </template>

    </table>
  </div>
</template>


<script>
export default {
  name:'checkPage',
  data() {
    return {
      setState:0,
      data:{},
      otherData:{},
      isNon:false
    }
  },
  methods: {
    check()
    {
      var count =0; 
      for(var a in this.data)
        count++; 
      for(var a in this.otherData)
        if(this.otherData[a])
          count++; 
      if(this.question.expected && !count)
      { 
        return false
      }
      if(this.question.min && count<this.question.min)
      { 
        return false
      }  
      if(this.question.max && count>this.question.max)
      { 
        return false
      }   
      return {data:this.data,otherData:this.otherData} 
    },
    checkClick(answerId,isNon)
    { 
      if(!isNon && this.isNon)
        return
      this.data[answerId]=!this.data[answerId]
      if(this.data[answerId] && isNon)
      {
        this.isNon=true;
        this.data={}
        this.data[answerId]=true;
      }
      else
      {
        this.isNon=false;
      }
      this.setState++  
    }
  },
  props:{
    question:{default:{}}
  }
}
</script>