<template>
  <div>
    <div  class="col-lg-12" style="text-align: center" >   
        <div  class="col-lg-12"> 
            {{description}}
        </div> 
        <div  class="col-lg-12 " > 
            <button  v-if="!disableButton" type="button" v-on:click="next()" class="forward">{{buttonTitle}}</button>
        </div>
    </div>
    
  </div>
</template>

<script>
export default {
  name:"acceptPage",
  props:{
    description:{default:""},
    buttonTitle:{default:""},
    disableButton:{default:false},
  },
  methods: {
    next()
    {
      this.$emit("next");
    }
  },
}
</script>