<template>
  <div>
    <div  style="display: none">
        {{setState}}
    </div>  
    <table  style="text-align: right;" class="col-lg-12">
      <template  v-for="(answ,index) in question.answers" >
        <tr>  
          <td v-if="question.showNumber">{{index1+1}}</td>
          <td v-if="answ.url" style="width: 45px;">
            <img v-bind:src="answ.url" style="width: 40px;"></img>
          </td>
          <td v-if="!question.place" >{{answ.text}}</td>
          <td> 
            <span  v-if="showExpected[answ.id]" class="error" style="top:-20px">اجباری</span>
              <input v-if="question.place" type="number" class="form-control" inputmode="numeric" pattern="[0-9]*"
                 lang="en-150" v-on:change="replaceNumber(answ.id,$event)" 
                 v-model="data[answ.id]" v-bind:placeholder="answ.text" />
													
              <input v-if="!question.place" v-on:change="replaceNumber(answ.id,$event)" type="number" 
                inputmode="numeric" pattern="[0-9]*" lang="en-150" class="form-control" v-model="data[answ.id]"/>
            <div  style="float: left;"> 
                {{getSeperated(data[answ.id])}}       
            </div>   
          </td>
          
          <tr style="border-bottom:3pt solid blue;">
            <td colspan="5"></td>
          </tr>
        </tr>
      </template>
      
      <template v-if="question.otherAnswers">
        <tr v-for="(answ,index1) in question.otherAnswers ">
          <td v-if="question.showNumber">{{question.answers.length+ index1}}</td> 
          <td >
            <div class="form-group radio_input">
              <label style="width: 100%">
                <div class="row">
                  <div class=" col-2 ">
                    <span v-if="!question.place ">{{answ.text}}</span>
                  </div>
                  <span class=" col-10">
                    <input  v-if="question.place" type="text" class="form-control" v-model="otherData[answ.id]" v-bind:placeholder="answ.text"/>
                    <input  v-if="!question.place" type="text" class="form-control" v-model="otherData[answ.id]"/>
                  </span>
                </div>
              </label>
            </div>
          </td>
        </tr>
      </template>

    </table>
    
  </div>
</template>

<script>
export default {
  name:'decimalPage',
  data() {
    return {
      setState:0,
      showExpected:{},
      data:{},
      otherData:{}
    }
  },
  methods: {
    getSeperated(num)
    {
      if(!num)
        return""
      var c=0
      var ch="";
      for(var a=num.length-1;a>0;a--)
      {
        c++;
        ch=num[a]+ch
        if(c==3)
        {
          c=0
          ch=","+ch
        }
      }
      if(num.length)
        ch= num[0]+ch
      return ch

    },
    replaceNumber(id,eve){
      var txt=this.data[id]
      var convertedText="";
      for(var  x in txt)
      {
        var a= txt[x];
        if(a=="٠")a="0";
        if(a=="١")a="1";
        if(a=="٢")a="2";
        if(a=="٣")a="3";
        if(a=="٤")a="4";
        if(a=="٥")a="5";
        if(a=="٦")a="6";
        if(a=="٧")a="7";
        if(a=="٨")a="8";
        if(a=="٩")a="9";
        if(a=="۰")a="0";
        if(a=="۱")a="1";
        if(a=="۲")a="2";
        if(a=="۳")a="3";
        if(a=="۴")a="4";
        if(a=="۵")a="5";
        if(a=="۶")a="6";
        if(a=="۷")a="7";
        if(a=="۸")a="8";
        if(a=="۹")a="9";
        convertedText+=a;
      }
      this.data[id]=convertedText;
      this.setState++;
    },
    check()
    {
      var isValid =true;
      for(var q of this.question.answers)
      {
        if(q.expected)
        {
          if(!this.data[q.id])
          {
            this.showExpected[q.id]=true
            isValid=false;
          }
          else  
            this.showExpected[q.id]=false 
        }
      }
      this.setState++;
      if(isValid)
      {
        return {data:this.data,otherData:this.otherData}
      }
      return isValid;
    }
  },
  mounted() {
    // for(var q of this.question.answers)
    // {  
    //   this.data[q.id]=1
    // }
    this.setState++;
    
  },
  props:{
    question:{default:{}}
  }
}
</script>